html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
  padding-top: 5rem;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.copyright {
  text-align: center;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
}

.dark-mode {
  background-color: #1e1f24;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode .navegacion a {
  color: #FFFFFF;
}
.dark-mode .navegacion a:hover {
  color: #F59E0B;
}
.dark-mode h1,
.dark-mode h2,
.dark-mode h3,
.dark-mode h4 {
  color: #FFFFFF;
}
.dark-mode .barra {
  background-color: #2a2b32;
}
.dark-mode .anuncio,
.dark-mode .resumen-propiedad {
  background-color: #2a2b32;
  border: 1px solid #2a2b32;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-propiedad .precio {
  color: rgb(0, 120, 0);
}
.dark-mode .anuncio .icono,
.dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #FFFFFF;
}
.dark-mode .formulario .campo input:not([type=submit]),
.dark-mode .formulario .campo textarea,
.dark-mode .formulario .campo select, .dark-mode .formulario .campo option {
  background-color: #2a2b32;
  border: 1px solid #2a2b32;
  color: #FFFFFF;
}
.dark-mode .formulario .campo select, .dark-mode .formulario .campo #imagen {
  color: #6b7280;
}

.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
}
.formulario .campo input, .formulario .campo textarea, .formulario .campo select {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
}
.formulario .campo select, .formulario .campo #imagen {
  color: #6b7280;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #000;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.header {
  border-bottom: 1px solid #6b7280;
}

.cont {
  width: 95%;
  margin: 0 auto;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.barra {
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 0;
  margin: 0;
  position: fixed;
  left: 0;
  right: 0;
  border-bottom: 2px solid #F59E0B;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}
.derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  filter: invert(100%);
}
.derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}

.izquierda {
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .izquierda {
    align-items: flex-end;
    flex-direction: row;
  }
}
.izquierda img {
  margin: 2 0 2 2;
  max-width: 15rem;
}
.izquierda a {
  width: 15vh;
}
.izquierda h1 {
  text-transform: uppercase;
  color: #FFFFFF;
}

.navegacion {
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
  margin: 3rem 0 0 0;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
    margin: 0;
  }
}
.navegacion a {
  color: #000;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
  .navegacion a:hover {
    color: #F59E0B;
  }
}

